                    </template> -->





                    <template v-slot:item.where="{item}">
                        <div class="d-flex justify-center">
                            <span class="blue--text" style="color: #2782F6!important;">
                                {{ item.where }}
                            </span>
                        </div>
                    </template>

                    <template v-slot:item.sum="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ formatPrice(item.sum) }} so`m</span>
