                                    class="mt-2" item-value="id" label="Tanlang"
                                    outlined :error-messages="errors" item-text="name">
                                </v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Nomni kiriting</span>
