                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal :deleteModal="deleteModal"></DeleteModal>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import { priceMixin } from '@/mixins/formatter.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [loaderMixin, priceMixin],
    components: {
        DeleteModal
    },
    data() {
        return {
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            headers: [
                {text: 'Qayerga', value: 'where', align: 'center'},
                {text: 'Summasi', value: 'sum', align: 'center'},
                {text: 'Sana', value: 'date', align: 'center'},
                // {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: [],
            items2: [],
        }
    },
    async mounted(){
        await this.refreshAllExpenses()
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                if (this.outcomeType == 'all_expenses') {
                    await this.refreshAllExpenses()
                }else if(this.outcomeType == 'outgoing_pay') {
                    await this.refreshOutgoingPay()
                }else if(this.outcomeType == 'product_income_pay') {
                    await this.refreshProductIncomePay()
                }else if(this.outcomeType == 'sewing_pay') {
                    await this.refreshSewingPay()
                }else if(this.outcomeType == 'expense_pay') {
                    await this.refreshExpansePay()
                }
            }
        },
        '$store.state.main.fctory_closed_orders': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_FACTORY_CLOSED_ORDERS', false)
                }
            },
            deep: true,
            immediate: true
        },
        outcomeType: async function (newVal, oldVal) {
            if (newVal == 'all_expenses') {
                await this.refreshAllExpenses();
            }else if(newVal == 'outgoing_pay') {
                await this.refreshOutgoingPay();
            }else if(newVal == 'product_income_pay') {
                await this.refreshProductIncomePay();
            }else if(newVal == 'sewing_pay') {
                await this.refreshSewingPay();
            }else if(newVal == 'expense_pay') {
                await this.refreshExpansePay();
            }
        },

    },
    computed: {
        ...mapState({
            outcomeType: state => state.finance.outcomeType,
            factory_sewing_history: state => state.factory.factory_sewing_history,
            all_expenses: state => state.finance.all_expenses,
            orderPayments: state => state.finance.orderPayments,
            productIncomePayments: state => state.finance.productIncomePayments,
            sewingPayments: state => state.finance.sewingPayments,
            expenses: state => state.finance.expenses,
        })
    },
    methods: {
        ...mapActions({
            fetchFactorySewingHistory: 'factory/fetchFactorySewingHistory',
            fetchExpenses: 'finance/fetchExpenses',
            fetchAllOrderPayments: 'finance/fetchAllOrderPayments',
            fetchAllProductIncomePayments: 'finance/fetchAllProductIncomePayments',
            fetchAllSewingPayments: 'finance/fetchAllSewingPayments',
            fetchExpansePayments: 'finance/fetchExpansePayments',
        }),
        async refreshAllExpenses(){
            this.loading = true

            await this.fetchExpenses({page: this.page});
            this.page = this.all_expenses.current_page;
            this.pageCount = Math.ceil(this.all_expenses.total / 20);
            this.items2 = this.all_expenses.data.slice(10, this.all_expenses.data.length);
            this.items = this.all_expenses.data;

            this.items.forEach(elem => {
                elem.where = elem.expense_name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.expense_name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async refreshOutgoingPay(){
            this.loading = true
            await this.fetchAllOrderPayments({page: this.page});
            this.page = this.orderPayments.current_page;
            this.pageCount = Math.ceil(this.orderPayments.total / 20);
            this.items2 = this.orderPayments.data.slice(10, this.orderPayments.data.length);
            this.items = this.orderPayments.data;

            this.items.forEach(elem => {
                elem.where = elem.order.factory.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.order.factory.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async refreshProductIncomePay(){
            this.loading = true
            await this.fetchAllProductIncomePayments({page: this.page});
            this.page = this.productIncomePayments.current_page;
            this.pageCount = Math.ceil(this.productIncomePayments.total / 20);
            this.items2 = this.productIncomePayments.data.slice(10, this.productIncomePayments.data.length);
            this.items = this.productIncomePayments.data;

            this.items.forEach(elem => {
                elem.where = elem.product_income.product.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.product_income.product.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async refreshSewingPay(){
            this.loading = true
            await this.fetchAllSewingPayments({page: this.page});
            this.page = this.sewingPayments.current_page;
            this.pageCount = Math.ceil(this.sewingPayments.total / 20);
            this.items2 = this.sewingPayments.data.slice(10, this.sewingPayments.data.length);
            this.items = this.sewingPayments.data;

            this.items.forEach(elem => {
                elem.where = elem.sewing_report.product.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.sewing_report.product.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async refreshExpansePay(){
            this.loading = true
            await this.fetchExpansePayments({page: this.page});
            this.page = this.expenses.current_page;
            this.pageCount = Math.ceil(this.expenses.total / 20);
            this.items2 = this.expenses.data.slice(10, this.expenses.data.length);
            this.items = this.expenses.data;

            this.items.forEach(elem => {
                elem.where = elem.description
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.items2.forEach(elem => {
                elem.where = elem.description
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        closeEditModal(){
            this.editModal = false
        }
    }
};
</script>

