<template>
    <div>
        <v-row>
            <v-col cols="12" md="12" lg="12" sm="12"
                style="border-right: 1px solid lightgray"
            >
                <v-data-table
                    :headers="headers"
                    :items="items"
                    item-key="name"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                >
                    <!-- eslint-disable  -->
                    <!-- <template v-slot:header.status="{ header }">
                        <span class="table-header">{{ header.text }}</span>
                    </template> -->
                    <template v-slot:header.name="{ header }">
                        <span class="table-header">{{ header.text }}</span>
                    </template>
                    <template v-slot:header.total_debt="{ header }">
                        <span class="table-header">{{ header.text }}</span>
                    </template>
                    <template v-slot:header.total_paid="{ header }">
                        <span class="table-header">{{ header.text }}</span>
                    </template>
                    <template v-slot:header.created_at="{ header }">
                        <span class="table-header">{{ header.text }}</span>
                    </template>
                    <template v-slot:header.actions="{ header }">
                        <span class="table-header">{{ header.text }}</span>
                    </template>






                    <!-- <template v-slot:item.status="{item}">
                        <div class="d-flex">
                            <img src="@/assets/images/enterimg.svg" alt="enterimg">
                        </div>
                    </template> -->
                    <template v-slot:item.name="{item}">
                        <div class="d-flex">
                            <span class="text-black" style="color: #2782F6!important;">
                                {{ item.name }}
                            </span>
                        </div>
                    </template>

                    <template v-slot:item.total_debt="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ formatPrice(item.total_debt) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.total_paid="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ formatPrice(item.total_paid) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.created_at="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ $dayjs(item.created_at).format('DD-MM-YYYY') }}</span>
                        </div>
                    </template>

                    <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center">
                            <div class="cursor-pointer mr-2 mt-1" v-if="$can('read', 'finance_kirim')"
                            @click="(paymentModal = true) && (element = item)">
                                <img src="@/assets/icons/add-square-green.svg"
                                 alt="add-square-green" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'finance_kirim')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1" v-if="$can('delete', 'finance_kirim')">
                                <img src="@/assets/icons/trush-square.svg" alt="trush-square">
                            </div>
                        </div>
                    </template>
                </v-data-table>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>


        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>


                <ValidationObserver ref="formEditIncomeThread" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">

                        <!-- <div class="mt-4">
                            <span class="text-black ml-1">Turni tanlang</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'code']}"
                              v-slot="{ errors }">
                                <v-select :items="filteredItems" v-model="product.product_category_id"
                                    class="mt-2" item-value="id" label="Tanlang"
                                    outlined :error-messages="errors" item-text="name">
                                </v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Nomni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'from_where']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.name" class="mt-2"
                                    placeholder="Misol: Vilvit" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Kodni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'name']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.code" type="number" class="mt-2"
                                    placeholder="Misol: 666777" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Tavsif kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'description']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.description" class="mt-2"
                                    placeholder="Misol: bu yashil ip" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div> -->


                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditModal"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>


        <DeleteModal :deleteModal="deleteModal"></DeleteModal>
        <ModalIncomePayment :paymentModal="paymentModal" :item="element"></ModalIncomePayment>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import {priceMixin} from '@/mixins/formatter.js';
import {loaderMixin} from '@/mixins/dateLoaderMixins.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'
import ModalIncomePayment from '@/components/modals/Finance/ModalIncomePayment'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        DeleteModal,
        ModalIncomePayment
    },
    data() {
        return {
            page: 1,
            pageCount: 0,
            deleteModal: false,
            paymentModal: false,
            items: [],
            items2: [],
            headers: [
                // {text: 'Status', value: 'status'},
                {text: 'Nomi', value: 'name',},
                {text: 'Umumiy qarzdorlik', value: 'total_debt'},
                {text: 'Umumiy berildi', value: 'total_paid'},
                {text: 'Sana', value: 'created_at'},
                {text: 'Harakatlar', value: 'actions'},
            ],
            element: {}
        }
    },
    watch: {
        page: function (newVal, oldVal) {
            if (newVal) {
                this.refresh()
            }
        }
    },
    async mounted() {
        await this.refresh();
    },
    computed: {
        ...mapState(
            { debtors: state => state.finance.debtors }
        ),
    },
    methods: {
        ...mapActions(
            {fetchDebtors: 'finance/fetchDebtors'}
        ),
        async refresh() {
            this.loading = true
            await this.fetchDebtors({page: this.page});

            this.page = this.debtors.current_page;
            this.pageCount = Math.ceil(this.debtors.total / 20);
            this.items = this.debtors.data;
            this.loading = false

            this.items.forEach(element => {
                element.name = element.client.name
            });
           
        },
        async updateTable(e) {
            if (e) {
                await this.refresh();
            }
        },
    }
};
</script>

